@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
.fadeTop {
    -webkit-animation: fadeInTop 1s ease-in both;
            animation: fadeInTop 1s ease-in both;
}
.fadeRight {
    -webkit-animation: fadeInRight 1s ease-in both;
            animation: fadeInRight 1s ease-in both;
}
.fadeLeft {
    -webkit-animation: fadeInLeft 1s ease-in both;
            animation: fadeInLeft 1s ease-in both;
}
.fadeBottom {
    -webkit-animation: fadeInBottom 1s ease-in both;
            animation: fadeInBottom 1s ease-in both;
}
.fadeScale {
    -webkit-animation: fadeScale 1s ease-in both;
            animation: fadeScale 1s ease-in both;
}
@-webkit-keyframes fadeInTop {
	from {
		opacity: 0;
		-webkit-transform: translate3d(0, -20%, 0);
		        transform: translate3d(0, -20%, 0);
	}
	to {
		opacity: 1;
		-webkit-transform: translate3d(0, 0, 0);
		        transform: translate3d(0, 0, 0);
	}
}
@keyframes fadeInTop {
	from {
		opacity: 0;
		-webkit-transform: translate3d(0, -20%, 0);
		        transform: translate3d(0, -20%, 0);
	}
	to {
		opacity: 1;
		-webkit-transform: translate3d(0, 0, 0);
		        transform: translate3d(0, 0, 0);
	}
}
@-webkit-keyframes fadeInRight {
    from {
        opacity: 0;
        -webkit-transform: translate3d(-20%, 0, 0);
                transform: translate3d(-20%, 0, 0);
    }
    to {
		opacity: 1;
		-webkit-transform: translate3d(0, 0, 0);
		        transform: translate3d(0, 0, 0);
	}
}
@keyframes fadeInRight {
    from {
        opacity: 0;
        -webkit-transform: translate3d(-20%, 0, 0);
                transform: translate3d(-20%, 0, 0);
    }
    to {
		opacity: 1;
		-webkit-transform: translate3d(0, 0, 0);
		        transform: translate3d(0, 0, 0);
	}
}
@-webkit-keyframes fadeInLeft {
    from {
        opacity: 0;
        -webkit-transform: translate3d(20%, 0, 0);
                transform: translate3d(20%, 0, 0);
    }
    to {
		opacity: 1;
		-webkit-transform: translate3d(0, 0, 0);
		        transform: translate3d(0, 0, 0);
	}
}
@keyframes fadeInLeft {
    from {
        opacity: 0;
        -webkit-transform: translate3d(20%, 0, 0);
                transform: translate3d(20%, 0, 0);
    }
    to {
		opacity: 1;
		-webkit-transform: translate3d(0, 0, 0);
		        transform: translate3d(0, 0, 0);
	}
}
@-webkit-keyframes fadeInBottom {
    from {
        opacity: 0;
        -webkit-transform: translate3d(0, 20%, 0);
                transform: translate3d(0, 20%, 0);
    }
    to {
		opacity: 1;
		-webkit-transform: translate3d(0, 0, 0);
		        transform: translate3d(0, 0, 0);
	}
}
@keyframes fadeInBottom {
    from {
        opacity: 0;
        -webkit-transform: translate3d(0, 20%, 0);
                transform: translate3d(0, 20%, 0);
    }
    to {
		opacity: 1;
		-webkit-transform: translate3d(0, 0, 0);
		        transform: translate3d(0, 0, 0);
	}
}
@-webkit-keyframes fadeScale{
    from{
        opacity: 0;
        -webkit-transform: scale(0.1);
                transform: scale(0.1);
    }
    to{
        opacity: 1;
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}
@keyframes fadeScale{
    from{
        opacity: 0;
        -webkit-transform: scale(0.1);
                transform: scale(0.1);
    }
    to{
        opacity: 1;
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}
